<template>
  <div>
    <NarBar/>
    <div class="containers mr-tt">
      <a-row class="items" :gutter="24">
        <a-col :span="16">
          <div style="background: #fff" class="shadow">
            <div class="head">
              <div style="width: 100%;">
                <img width="100%" height="200" src="https://cdn.jsdelivr.net/gh/Bursteretion/file-repo/imgs/wallhaven-2el5zg.jpg">
              </div>
              <div class="h1 mr-t" style="text-align: center">
                {{article.title}}
              </div>
              <div class="mr-t" style="text-align: center">
              <span>
                <a-icon type="calendar" />
                  {{article.publish_time}}
                </span>
                <span class="mr-l">
                  <a-icon type="user" />
                    {{article.author}}
                  </span>
              </div>
            </div>
            <mavon-editor
              class="md"
              :boxShadow="false"
              :codeStyle="'tomorrow-night'"
              :value="article.contents"
              :subfield="prop.subfield"
              :defaultOpen="prop.defaultOpen"
              :toolbarsFlag="prop.toolbarsFlag"
              :editable="prop.editable"
              :scrollStyle="prop.scrollStyle"
              :boxShadowStyle="'none'"
            />
          </div>
          <div class="mr-tt">
            <div id="gitalk-container"></div>
          </div>
        </a-col>
        <a-col class="right-items" :span="8">
          <Profile/>
          <Category/>
          <Tag/>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
  import NarBar from "./components/NarBar";
  import Profile from "./components/Profile";
  import Category from "./components/Category";
  import Tag from './components/Tag'
  import ArticleApi from "../../api/Articles";
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  export default {
    name: "About",
    components: {
      NarBar,
      Profile,
      Category,
      Tag,
      mavonEditor
    },
    data() {
      return {
        article: {},
        id: '',
        // eslint-disable-next-line no-undef
        gitalk : new Gitalk({
          clientID: '1254cc140effd03a56ef',
          clientSecret: '0b4c6e268522d6c4a27a809b8705e95b877177ca',
          repo: 'gitalk',
          owner: 'Bursteretion',
          admin: ['Bursteretion'],
          id: location.pathname,      // Ensure uniqueness and length less than 50
          distractionFreeMode: false  // Facebook-like distraction free mode
        }),
      }
    },
    computed: {
      // 解析器配置
      prop () {
        let data = {
          subfield: false,// 单双栏模式
          defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域
          editable: false,	// 是否允许编辑
          toolbarsFlag: false,
          scrollStyle: true
        }
        return data
      }
    },
    mounted () {
      this.gitalk.render("gitalk-container");
    },
    created () {
      this.loadArticle()
    },
    methods: {
      async loadArticle() {
        ArticleApi.me().then(res => {
          if (res.code === 1000) {
            this.article = res.data.article
          } else {
            this.$message.error('获取文章失败')
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .right-items > *:not(:first-child) {
    margin-top: 10px;
  }
  .card {
    padding: 20px;
  }
  @media screen and (max-width: 800px) {
    .containers {
      width: 95%;
      padding-right: 0 !important;
    }
    .items > * {
      padding-left: 12px;
      padding-right: 12px;
      width: 100%;
    }
    .items > *:last-child {
      margin-top: 10px;
    }
  }
</style>